'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import {
  DialerScript,
  ScriptSection,
  ObjectionHandler,
  DynamicField
} from '@/lib/power-dialer/types'

// ==========================================
// CALL SCRIPTS MANAGEMENT PAGE
// ==========================================

export default function CallScriptsPage() {
  const [scripts, setScripts] = useState<DialerScript[]>([])
  const [loading, setLoading] = useState(true)
  const [selectedScript, setSelectedScript] = useState<DialerScript | null>(null)
  const [isEditing, setIsEditing] = useState(false)
  const [showNewScriptModal, setShowNewScriptModal] = useState(false)

  // TODO: Get from auth context
  const subAccountId = 'b0000000-0000-0000-0000-000000000001'

  useEffect(() => {
    fetchScripts()
  }, [])

  const fetchScripts = async () => {
    setLoading(true)
    try {
      const res = await fetch(`/api/v1/dialer/scripts?subAccountId=${subAccountId}&activeOnly=false`)
      const data = await res.json()
      if (data.success) {
        setScripts(data.data)
      }
    } catch (err) {
      console.error('Error fetching scripts:', err)
    } finally {
      setLoading(false)
    }
  }

  const deleteScript = async (scriptId: string) => {
    if (!confirm('Are you sure you want to delete this script?')) return

    try {
      const res = await fetch(`/api/v1/dialer/scripts?id=${scriptId}&subAccountId=${subAccountId}`, {
        method: 'DELETE'
      })
      if (res.ok) {
        setScripts(prev => prev.filter(s => s.id !== scriptId))
        if (selectedScript?.id === scriptId) {
          setSelectedScript(null)
        }
      }
    } catch (err) {
      console.error('Error deleting script:', err)
    }
  }

  return (
    <div className="p-6 max-w-7xl mx-auto">
      {/* Header */}
      <div className="flex justify-between items-center mb-6">
        <div>
          <div className="flex items-center gap-3">
            <Link href="/sub-account/power-dialer" className="text-gray-400 hover:text-gray-600">
              <i className="fas fa-arrow-left"></i>
            </Link>
            <h1 className="text-2xl font-bold text-gray-900 flex items-center gap-2">
              <i className="fas fa-scroll text-green-600"></i>
              Call Scripts
            </h1>
          </div>
          <p className="text-gray-500 text-sm mt-1 ml-8">
            Create and manage scripts for your dialing campaigns
          </p>
        </div>
        <button
          onClick={() => setShowNewScriptModal(true)}
          className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 flex items-center gap-2"
        >
          <i className="fas fa-plus"></i>
          New Script
        </button>
      </div>

      {loading ? (
        <div className="flex justify-center items-center h-64">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
        </div>
      ) : (
        <div className="flex gap-6">
          {/* Scripts List */}
          <div className="w-1/3 space-y-3">
            {scripts.length === 0 ? (
              <div className="bg-white rounded-xl shadow-sm border p-8 text-center">
                <i className="fas fa-scroll text-4xl text-gray-300 mb-3"></i>
                <p className="text-gray-500">No scripts yet</p>
                <p className="text-sm text-gray-400">Create your first call script</p>
              </div>
            ) : (
              scripts.map(script => (
                <div
                  key={script.id}
                  onClick={() => {
                    setSelectedScript(script)
                    setIsEditing(false)
                  }}
                  className={`bg-white rounded-xl shadow-sm border p-4 cursor-pointer transition-all ${
                    selectedScript?.id === script.id
                      ? 'ring-2 ring-blue-500 border-blue-500'
                      : 'hover:border-gray-300'
                  }`}
                >
                  <div className="flex justify-between items-start mb-2">
                    <div className="flex items-center gap-2">
                      <h3 className="font-medium text-gray-900">{script.name}</h3>
                      {script.isDefault && (
                        <span className="text-xs bg-blue-100 text-blue-700 px-2 py-0.5 rounded">
                          Default
                        </span>
                      )}
                    </div>
                    <span className={`text-xs px-2 py-0.5 rounded ${
                      script.isActive 
                        ? 'bg-green-100 text-green-700'
                        : 'bg-gray-100 text-gray-500'
                    }`}>
                      {script.isActive ? 'Active' : 'Inactive'}
                    </span>
                  </div>
                  {script.description && (
                    <p className="text-sm text-gray-500 line-clamp-2">{script.description}</p>
                  )}
                  <div className="flex items-center gap-4 mt-3 text-xs text-gray-400">
                    <span><i className="fas fa-list-ol mr-1"></i>{script.sections?.length || 0} sections</span>
                    <span><i className="fas fa-shield-alt mr-1"></i>{script.objectionHandlers?.length || 0} objections</span>
                    <span><i className="fas fa-chart-line mr-1"></i>{script.timesUsed} uses</span>
                  </div>
                </div>
              ))
            )}
          </div>

          {/* Script Detail/Editor */}
          <div className="flex-1">
            {selectedScript ? (
              <ScriptEditor
                script={selectedScript}
                isEditing={isEditing}
                onEdit={() => setIsEditing(true)}
                onSave={async (updatedScript) => {
                  try {
                    const res = await fetch('/api/v1/dialer/scripts', {
                      method: 'PUT',
                      headers: { 'Content-Type': 'application/json' },
                      body: JSON.stringify({
                        id: selectedScript.id,
                        subAccountId,
                        ...updatedScript
                      })
                    })
                    const data = await res.json()
                    if (data.success) {
                      setSelectedScript(data.data)
                      setScripts(prev => prev.map(s => 
                        s.id === data.data.id ? data.data : s
                      ))
                      setIsEditing(false)
                    }
                  } catch (err) {
                    console.error('Error saving script:', err)
                  }
                }}
                onCancel={() => setIsEditing(false)}
                onDelete={() => deleteScript(selectedScript.id)}
              />
            ) : (
              <div className="bg-white rounded-xl shadow-sm border p-12 text-center">
                <i className="fas fa-hand-pointer text-5xl text-gray-300 mb-4"></i>
                <p className="text-gray-500">Select a script to view or edit</p>
              </div>
            )}
          </div>
        </div>
      )}

      {/* New Script Modal */}
      {showNewScriptModal && (
        <NewScriptModal
          onClose={() => setShowNewScriptModal(false)}
          onCreated={(newScript) => {
            setScripts(prev => [...prev, newScript])
            setSelectedScript(newScript)
            setShowNewScriptModal(false)
          }}
          subAccountId={subAccountId}
        />
      )}
    </div>
  )
}

// ==========================================
// SCRIPT EDITOR COMPONENT
// ==========================================

function ScriptEditor({
  script,
  isEditing,
  onEdit,
  onSave,
  onCancel,
  onDelete
}: {
  script: DialerScript
  isEditing: boolean
  onEdit: () => void
  onSave: (script: Partial<DialerScript>) => void
  onCancel: () => void
  onDelete: () => void
}) {
  const [formData, setFormData] = useState({
    name: script.name,
    description: script.description || '',
    category: script.category || '',
    isDefault: script.isDefault,
    isActive: script.isActive,
    greeting: script.greeting || '',
    introduction: script.introduction || '',
    sections: script.sections || [],
    objectionHandlers: script.objectionHandlers || [],
    closingPositive: script.closingPositive || '',
    closingNegative: script.closingNegative || '',
    voicemailScript: script.voicemailScript || '',
    dynamicFields: script.dynamicFields || []
  })
  const [activeTab, setActiveTab] = useState<'script' | 'objections' | 'fields'>('script')

  // Reset form when script changes
  useEffect(() => {
    setFormData({
      name: script.name,
      description: script.description || '',
      category: script.category || '',
      isDefault: script.isDefault,
      isActive: script.isActive,
      greeting: script.greeting || '',
      introduction: script.introduction || '',
      sections: script.sections || [],
      objectionHandlers: script.objectionHandlers || [],
      closingPositive: script.closingPositive || '',
      closingNegative: script.closingNegative || '',
      voicemailScript: script.voicemailScript || '',
      dynamicFields: script.dynamicFields || []
    })
  }, [script])

  const addSection = () => {
    setFormData(prev => ({
      ...prev,
      sections: [
        ...prev.sections,
        {
          id: `section-${Date.now()}`,
          title: 'New Section',
          content: '',
          type: 'text' as const,
          order: prev.sections.length
        }
      ]
    }))
  }

  const updateSection = (index: number, updates: Partial<ScriptSection>) => {
    setFormData(prev => ({
      ...prev,
      sections: prev.sections.map((s, i) => 
        i === index ? { ...s, ...updates } : s
      )
    }))
  }

  const removeSection = (index: number) => {
    setFormData(prev => ({
      ...prev,
      sections: prev.sections.filter((_, i) => i !== index)
    }))
  }

  const addObjectionHandler = () => {
    setFormData(prev => ({
      ...prev,
      objectionHandlers: [
        ...prev.objectionHandlers,
        {
          id: `objection-${Date.now()}`,
          objection: '',
          response: ''
        }
      ]
    }))
  }

  const updateObjectionHandler = (index: number, updates: Partial<ObjectionHandler>) => {
    setFormData(prev => ({
      ...prev,
      objectionHandlers: prev.objectionHandlers.map((h, i) => 
        i === index ? { ...h, ...updates } : h
      )
    }))
  }

  const removeObjectionHandler = (index: number) => {
    setFormData(prev => ({
      ...prev,
      objectionHandlers: prev.objectionHandlers.filter((_, i) => i !== index)
    }))
  }

  const addDynamicField = () => {
    setFormData(prev => ({
      ...prev,
      dynamicFields: [
        ...prev.dynamicFields,
        {
          placeholder: '',
          field: '',
          defaultValue: ''
        }
      ]
    }))
  }

  const updateDynamicField = (index: number, updates: Partial<DynamicField>) => {
    setFormData(prev => ({
      ...prev,
      dynamicFields: prev.dynamicFields.map((f, i) => 
        i === index ? { ...f, ...updates } : f
      )
    }))
  }

  const removeDynamicField = (index: number) => {
    setFormData(prev => ({
      ...prev,
      dynamicFields: prev.dynamicFields.filter((_, i) => i !== index)
    }))
  }

  return (
    <div className="bg-white rounded-xl shadow-sm border">
      {/* Header */}
      <div className="p-4 border-b flex justify-between items-center">
        <div>
          {isEditing ? (
            <input
              type="text"
              value={formData.name}
              onChange={(e) => setFormData({ ...formData, name: e.target.value })}
              className="text-xl font-bold text-gray-900 border-b-2 border-blue-500 focus:outline-none"
            />
          ) : (
            <h2 className="text-xl font-bold text-gray-900">{script.name}</h2>
          )}
          <div className="flex items-center gap-2 mt-1">
            {script.isDefault && (
              <span className="text-xs bg-blue-100 text-blue-700 px-2 py-0.5 rounded">Default</span>
            )}
            <span className={`text-xs px-2 py-0.5 rounded ${
              script.isActive ? 'bg-green-100 text-green-700' : 'bg-gray-100 text-gray-500'
            }`}>
              {script.isActive ? 'Active' : 'Inactive'}
            </span>
          </div>
        </div>
        <div className="flex gap-2">
          {isEditing ? (
            <>
              <button
                onClick={onCancel}
                className="px-4 py-2 border rounded-lg text-gray-700 hover:bg-gray-50"
              >
                Cancel
              </button>
              <button
                onClick={() => onSave(formData)}
                className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
              >
                Save Changes
              </button>
            </>
          ) : (
            <>
              <button
                onClick={onDelete}
                className="px-4 py-2 text-red-600 hover:bg-red-50 rounded-lg"
              >
                <i className="fas fa-trash"></i>
              </button>
              <button
                onClick={onEdit}
                className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 flex items-center gap-2"
              >
                <i className="fas fa-edit"></i>
                Edit Script
              </button>
            </>
          )}
        </div>
      </div>

      {/* Tabs */}
      <div className="border-b px-4">
        <div className="flex gap-4">
          {[
            { id: 'script', label: 'Script Content', icon: 'fas fa-scroll' },
            { id: 'objections', label: 'Objection Handlers', icon: 'fas fa-shield-alt' },
            { id: 'fields', label: 'Dynamic Fields', icon: 'fas fa-code' }
          ].map(tab => (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id as typeof activeTab)}
              className={`py-3 border-b-2 flex items-center gap-2 transition-colors ${
                activeTab === tab.id
                  ? 'border-blue-600 text-blue-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700'
              }`}
            >
              <i className={tab.icon}></i>
              {tab.label}
            </button>
          ))}
        </div>
      </div>

      {/* Content */}
      <div className="p-6 max-h-[calc(100vh-300px)] overflow-y-auto">
        {activeTab === 'script' && (
          <div className="space-y-6">
            {/* Description */}
            {isEditing && (
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Description</label>
                <textarea
                  value={formData.description}
                  onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                  className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500"
                  rows={2}
                  placeholder="Script description..."
                />
              </div>
            )}

            {/* Settings */}
            {isEditing && (
              <div className="flex gap-4">
                <label className="flex items-center gap-2">
                  <input
                    type="checkbox"
                    checked={formData.isDefault}
                    onChange={(e) => setFormData({ ...formData, isDefault: e.target.checked })}
                    className="rounded border-gray-300 text-blue-600"
                  />
                  <span className="text-sm text-gray-700">Default Script</span>
                </label>
                <label className="flex items-center gap-2">
                  <input
                    type="checkbox"
                    checked={formData.isActive}
                    onChange={(e) => setFormData({ ...formData, isActive: e.target.checked })}
                    className="rounded border-gray-300 text-blue-600"
                  />
                  <span className="text-sm text-gray-700">Active</span>
                </label>
              </div>
            )}

            {/* Greeting */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                <i className="fas fa-hand-wave mr-1 text-blue-500"></i>
                Greeting
              </label>
              {isEditing ? (
                <textarea
                  value={formData.greeting}
                  onChange={(e) => setFormData({ ...formData, greeting: e.target.value })}
                  className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500"
                  rows={2}
                  placeholder="Hi, is this {{name}}?"
                />
              ) : (
                <div className="bg-blue-50 border border-blue-200 rounded-lg p-3 text-gray-700">
                  {script.greeting || <span className="text-gray-400 italic">No greeting set</span>}
                </div>
              )}
            </div>

            {/* Introduction */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                <i className="fas fa-comment mr-1 text-purple-500"></i>
                Introduction
              </label>
              {isEditing ? (
                <textarea
                  value={formData.introduction}
                  onChange={(e) => setFormData({ ...formData, introduction: e.target.value })}
                  className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500"
                  rows={3}
                  placeholder="My name is [Agent Name] from [Company]..."
                />
              ) : (
                <div className="bg-purple-50 border border-purple-200 rounded-lg p-3 text-gray-700">
                  {script.introduction || <span className="text-gray-400 italic">No introduction set</span>}
                </div>
              )}
            </div>

            {/* Main Sections */}
            <div>
              <div className="flex justify-between items-center mb-2">
                <label className="block text-sm font-medium text-gray-700">
                  <i className="fas fa-list-ol mr-1 text-green-500"></i>
                  Main Script Sections
                </label>
                {isEditing && (
                  <button
                    onClick={addSection}
                    className="text-sm text-blue-600 hover:text-blue-700 flex items-center gap-1"
                  >
                    <i className="fas fa-plus"></i>
                    Add Section
                  </button>
                )}
              </div>
              
              <div className="space-y-3">
                {formData.sections.map((section, index) => (
                  <div key={section.id} className="bg-gray-50 border rounded-lg p-4">
                    {isEditing ? (
                      <div className="space-y-3">
                        <div className="flex gap-2">
                          <input
                            type="text"
                            value={section.title}
                            onChange={(e) => updateSection(index, { title: e.target.value })}
                            className="flex-1 px-3 py-2 border rounded focus:ring-2 focus:ring-blue-500"
                            placeholder="Section title"
                          />
                          <select
                            value={section.type}
                            onChange={(e) => updateSection(index, { type: e.target.value as ScriptSection['type'] })}
                            className="px-3 py-2 border rounded bg-white"
                          >
                            <option value="text">Text</option>
                            <option value="question">Question</option>
                            <option value="objection_handler">Objection Handler</option>
                          </select>
                          <button
                            onClick={() => removeSection(index)}
                            className="px-3 py-2 text-red-600 hover:bg-red-50 rounded"
                          >
                            <i className="fas fa-trash"></i>
                          </button>
                        </div>
                        <textarea
                          value={section.content}
                          onChange={(e) => updateSection(index, { content: e.target.value })}
                          className="w-full px-3 py-2 border rounded focus:ring-2 focus:ring-blue-500"
                          rows={3}
                          placeholder="Section content..."
                        />
                      </div>
                    ) : (
                      <>
                        <div className="flex items-center gap-2 mb-2">
                          <span className="w-6 h-6 bg-blue-100 text-blue-600 rounded-full flex items-center justify-center text-sm font-medium">
                            {index + 1}
                          </span>
                          <h4 className="font-medium text-gray-900">{section.title}</h4>
                          {section.type !== 'text' && (
                            <span className="text-xs bg-yellow-100 text-yellow-700 px-2 py-0.5 rounded capitalize">
                              {section.type}
                            </span>
                          )}
                        </div>
                        <p className="text-gray-600 whitespace-pre-wrap">{section.content}</p>
                      </>
                    )}
                  </div>
                ))}
                {formData.sections.length === 0 && (
                  <div className="text-center py-6 text-gray-400">
                    <p>No sections yet</p>
                    {isEditing && <p className="text-sm">Click &quot;Add Section&quot; to add one</p>}
                  </div>
                )}
              </div>
            </div>

            {/* Closing Positive */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                <i className="fas fa-thumbs-up mr-1 text-green-500"></i>
                Closing (Positive Outcome)
              </label>
              {isEditing ? (
                <textarea
                  value={formData.closingPositive}
                  onChange={(e) => setFormData({ ...formData, closingPositive: e.target.value })}
                  className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500"
                  rows={2}
                  placeholder="Great! Let me schedule that for you..."
                />
              ) : (
                <div className="bg-green-50 border border-green-200 rounded-lg p-3 text-gray-700">
                  {script.closingPositive || <span className="text-gray-400 italic">No positive closing set</span>}
                </div>
              )}
            </div>

            {/* Closing Negative */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                <i className="fas fa-thumbs-down mr-1 text-red-500"></i>
                Closing (Negative Outcome)
              </label>
              {isEditing ? (
                <textarea
                  value={formData.closingNegative}
                  onChange={(e) => setFormData({ ...formData, closingNegative: e.target.value })}
                  className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500"
                  rows={2}
                  placeholder="I understand. Can I leave you with my contact information..."
                />
              ) : (
                <div className="bg-red-50 border border-red-200 rounded-lg p-3 text-gray-700">
                  {script.closingNegative || <span className="text-gray-400 italic">No negative closing set</span>}
                </div>
              )}
            </div>

            {/* Voicemail */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                <i className="fas fa-voicemail mr-1 text-orange-500"></i>
                Voicemail Script
              </label>
              {isEditing ? (
                <textarea
                  value={formData.voicemailScript}
                  onChange={(e) => setFormData({ ...formData, voicemailScript: e.target.value })}
                  className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500"
                  rows={3}
                  placeholder="Hi {{name}}, this is [Agent Name] from [Company]..."
                />
              ) : (
                <div className="bg-orange-50 border border-orange-200 rounded-lg p-3 text-gray-700">
                  {script.voicemailScript || <span className="text-gray-400 italic">No voicemail script set</span>}
                </div>
              )}
            </div>
          </div>
        )}

        {activeTab === 'objections' && (
          <div className="space-y-4">
            <div className="flex justify-between items-center">
              <p className="text-sm text-gray-500">
                Add common objections and responses to help agents handle difficult conversations.
              </p>
              {isEditing && (
                <button
                  onClick={addObjectionHandler}
                  className="px-4 py-2 text-blue-600 border border-blue-600 rounded-lg hover:bg-blue-50 flex items-center gap-2"
                >
                  <i className="fas fa-plus"></i>
                  Add Objection
                </button>
              )}
            </div>

            <div className="space-y-4">
              {formData.objectionHandlers.map((handler, index) => (
                <div key={handler.id} className="bg-gray-50 border rounded-lg p-4">
                  {isEditing ? (
                    <div className="space-y-3">
                      <div className="flex gap-2">
                        <div className="flex-1">
                          <label className="text-xs text-gray-500 mb-1 block">Objection</label>
                          <input
                            type="text"
                            value={handler.objection}
                            onChange={(e) => updateObjectionHandler(index, { objection: e.target.value })}
                            className="w-full px-3 py-2 border rounded focus:ring-2 focus:ring-blue-500"
                            placeholder="&quot;I'm not interested&quot;"
                          />
                        </div>
                        <button
                          onClick={() => removeObjectionHandler(index)}
                          className="self-end px-3 py-2 text-red-600 hover:bg-red-50 rounded"
                        >
                          <i className="fas fa-trash"></i>
                        </button>
                      </div>
                      <div>
                        <label className="text-xs text-gray-500 mb-1 block">Response</label>
                        <textarea
                          value={handler.response}
                          onChange={(e) => updateObjectionHandler(index, { response: e.target.value })}
                          className="w-full px-3 py-2 border rounded focus:ring-2 focus:ring-blue-500"
                          rows={3}
                          placeholder="I completely understand. May I ask what specifically doesn't interest you..."
                        />
                      </div>
                    </div>
                  ) : (
                    <>
                      <div className="flex items-start gap-3">
                        <div className="w-8 h-8 bg-orange-100 text-orange-600 rounded-full flex items-center justify-center flex-shrink-0">
                          <i className="fas fa-quote-left text-xs"></i>
                        </div>
                        <div>
                          <p className="font-medium text-orange-700">&quot;{handler.objection}&quot;</p>
                          <p className="mt-2 text-gray-600">{handler.response}</p>
                        </div>
                      </div>
                    </>
                  )}
                </div>
              ))}
              {formData.objectionHandlers.length === 0 && (
                <div className="text-center py-8 text-gray-400">
                  <i className="fas fa-shield-alt text-4xl mb-3 opacity-50"></i>
                  <p>No objection handlers yet</p>
                  {isEditing && <p className="text-sm">Add common objections to help agents</p>}
                </div>
              )}
            </div>
          </div>
        )}

        {activeTab === 'fields' && (
          <div className="space-y-4">
            <div className="flex justify-between items-center">
              <div>
                <p className="text-sm text-gray-500">
                  Dynamic fields are placeholders that get replaced with actual contact data.
                </p>
                <p className="text-xs text-gray-400 mt-1">
                  Use {`{{placeholder}}`} syntax in your script text.
                </p>
              </div>
              {isEditing && (
                <button
                  onClick={addDynamicField}
                  className="px-4 py-2 text-blue-600 border border-blue-600 rounded-lg hover:bg-blue-50 flex items-center gap-2"
                >
                  <i className="fas fa-plus"></i>
                  Add Field
                </button>
              )}
            </div>

            {/* Built-in Fields */}
            <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
              <h4 className="text-sm font-medium text-blue-700 mb-2">Built-in Fields</h4>
              <div className="flex flex-wrap gap-2">
                {[
                  { placeholder: '{{name}}', desc: 'Full name' },
                  { placeholder: '{{firstName}}', desc: 'First name' },
                  { placeholder: '{{phone}}', desc: 'Phone number' },
                  { placeholder: '{{email}}', desc: 'Email address' }
                ].map(field => (
                  <code key={field.placeholder} className="px-2 py-1 bg-blue-100 text-blue-700 rounded text-sm" title={field.desc}>
                    {field.placeholder}
                  </code>
                ))}
              </div>
            </div>

            {/* Custom Fields */}
            <div className="space-y-3">
              {formData.dynamicFields.map((field, index) => (
                <div key={index} className="bg-gray-50 border rounded-lg p-4">
                  {isEditing ? (
                    <div className="flex gap-3">
                      <div className="flex-1">
                        <label className="text-xs text-gray-500 mb-1 block">Placeholder</label>
                        <input
                          type="text"
                          value={field.placeholder}
                          onChange={(e) => updateDynamicField(index, { placeholder: e.target.value })}
                          className="w-full px-3 py-2 border rounded focus:ring-2 focus:ring-blue-500"
                          placeholder="company_name"
                        />
                      </div>
                      <div className="flex-1">
                        <label className="text-xs text-gray-500 mb-1 block">Contact Field</label>
                        <input
                          type="text"
                          value={field.field}
                          onChange={(e) => updateDynamicField(index, { field: e.target.value })}
                          className="w-full px-3 py-2 border rounded focus:ring-2 focus:ring-blue-500"
                          placeholder="customFields.company"
                        />
                      </div>
                      <div className="flex-1">
                        <label className="text-xs text-gray-500 mb-1 block">Default Value</label>
                        <input
                          type="text"
                          value={field.defaultValue}
                          onChange={(e) => updateDynamicField(index, { defaultValue: e.target.value })}
                          className="w-full px-3 py-2 border rounded focus:ring-2 focus:ring-blue-500"
                          placeholder="your company"
                        />
                      </div>
                      <button
                        onClick={() => removeDynamicField(index)}
                        className="self-end px-3 py-2 text-red-600 hover:bg-red-50 rounded"
                      >
                        <i className="fas fa-trash"></i>
                      </button>
                    </div>
                  ) : (
                    <div className="flex items-center gap-4">
                      <code className="px-2 py-1 bg-gray-200 rounded">{`{{${field.placeholder}}}`}</code>
                      <span className="text-gray-400">â†’</span>
                      <span className="text-gray-600">{field.field}</span>
                      {field.defaultValue && (
                        <>
                          <span className="text-gray-400">|</span>
                          <span className="text-gray-400 text-sm">default: &quot;{field.defaultValue}&quot;</span>
                        </>
                      )}
                    </div>
                  )}
                </div>
              ))}
              {formData.dynamicFields.length === 0 && (
                <div className="text-center py-6 text-gray-400">
                  <p>No custom dynamic fields</p>
                  {isEditing && <p className="text-sm">Add custom fields for personalization</p>}
                </div>
              )}
            </div>
          </div>
        )}
      </div>
    </div>
  )
}

// ==========================================
// NEW SCRIPT MODAL
// ==========================================

function NewScriptModal({
  onClose,
  onCreated,
  subAccountId
}: {
  onClose: () => void
  onCreated: (script: DialerScript) => void
  subAccountId: string
}) {
  const [formData, setFormData] = useState({
    name: '',
    description: '',
    category: '',
    isDefault: false
  })
  const [saving, setSaving] = useState(false)
  const [error, setError] = useState('')

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!formData.name.trim()) {
      setError('Script name is required')
      return
    }

    setSaving(true)
    setError('')

    try {
      const res = await fetch('/api/v1/dialer/scripts', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          subAccountId,
          ...formData,
          isActive: true,
          sections: [],
          objectionHandlers: [],
          dynamicFields: []
        })
      })

      const data = await res.json()
      if (data.success) {
        onCreated(data.data)
      } else {
        setError(data.error || 'Failed to create script')
      }
    } catch (err) {
      setError('Network error')
    } finally {
      setSaving(false)
    }
  }

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center">
      <div className="absolute inset-0 bg-black/50" onClick={onClose}></div>
      <div className="relative bg-white rounded-2xl shadow-xl w-full max-w-md mx-4">
        <div className="p-6">
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-xl font-bold text-gray-900">New Call Script</h2>
            <button onClick={onClose} className="text-gray-400 hover:text-gray-600">
              <i className="fas fa-times text-xl"></i>
            </button>
          </div>

          {error && (
            <div className="mb-4 p-3 bg-red-50 border border-red-200 rounded-lg text-red-700 text-sm">
              {error}
            </div>
          )}

          <form onSubmit={handleSubmit} className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Script Name *
              </label>
              <input
                type="text"
                value={formData.name}
                onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500"
                placeholder="e.g., Sales Introduction Script"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Description
              </label>
              <textarea
                value={formData.description}
                onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500"
                rows={2}
                placeholder="Brief description of this script..."
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Category
              </label>
              <select
                value={formData.category}
                onChange={(e) => setFormData({ ...formData, category: e.target.value })}
                className="w-full px-4 py-2 border rounded-lg bg-white"
              >
                <option value="">Select category</option>
                <option value="sales">Sales</option>
                <option value="support">Support</option>
                <option value="followup">Follow-up</option>
                <option value="survey">Survey</option>
                <option value="appointment">Appointment</option>
                <option value="other">Other</option>
              </select>
            </div>

            <label className="flex items-center gap-2">
              <input
                type="checkbox"
                checked={formData.isDefault}
                onChange={(e) => setFormData({ ...formData, isDefault: e.target.checked })}
                className="rounded border-gray-300 text-blue-600"
              />
              <span className="text-sm text-gray-700">Set as default script</span>
            </label>

            <div className="flex gap-3 pt-4">
              <button
                type="button"
                onClick={onClose}
                className="flex-1 px-4 py-2 border rounded-lg text-gray-700 hover:bg-gray-50"
              >
                Cancel
              </button>
              <button
                type="submit"
                disabled={saving}
                className="flex-1 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50"
              >
                {saving ? 'Creating...' : 'Create Script'}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}
